@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800&family=Manrope:wght@300;400;500;600;700;800&family=Cinzel:wght@400;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Manrope', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #050505;
    color: #ffffff;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Playfair Display', serif;
}

@layer base {
    :root {
        --background: 0 0% 2%;
        --foreground: 0 0% 98%;
        --card: 0 0% 7%;
        --card-foreground: 0 0% 98%;
        --popover: 0 0% 7%;
        --popover-foreground: 0 0% 98%;
        --primary: 45 100% 51%;
        --primary-foreground: 0 0% 0%;
        --secondary: 0 0% 15%;
        --secondary-foreground: 0 0% 98%;
        --muted: 0 0% 15%;
        --muted-foreground: 0 0% 64%;
        --accent: 45 100% 51%;
        --accent-foreground: 0 0% 0%;
        --destructive: 0 62.8% 30.6%;
        --destructive-foreground: 0 0% 98%;
        --border: 0 0% 15%;
        --input: 0 0% 15%;
        --ring: 45 100% 51%;
        --radius: 0.25rem;
    }
}

@layer base {
    * {
        @apply border-border;
    }
    body {
        @apply bg-[#050505] text-foreground;
    }
}

.gold-text {
    background: linear-gradient(to right, #F2D06B, #D4AF37, #B38F26);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.gold-border {
    border-image: linear-gradient(135deg, #B38F26 0%, #F2D06B 50%, #D4AF37 100%) 1;
}

@layer utilities {
    .text-balance {
        text-wrap: balance;
    }
}
